import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue"
import uploadImages from "../../components/common/upload-images.vue"
import uniPopup from "../../components/uni-popup/uni-popup.vue"
let changlook = ['所有人可见', '仅自己可见'];
export default {
	components:{
		uniNavBar,
		uploadImages,
		uniPopup
	},
	data() {
		return {
			popupShow:true, 
			isget: false,
			yinsi: '所有人可见',
			text: '',
			imagesList: []
		}
	},
	onBackPress() {
		if(!this.isget){
			uni.showModal({
				content: '是否保存为草稿',
				cancelText: '不保存',
				confirmText: '保存',
				success: (res) => {
					if (res.confirm) {
						console.log('保存');
					} else if (res.cancel) {
						console.log('不保存');
					}
					this.isget = true;
					uni.navigateBack({
						delta: 1
					});
				}
			});
			return true;
		}
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		//发布帖子
		submit(){
			console.log('发布帖子');
		},
		//修改帖子查看权限
		changelook(){
			uni.showActionSheet({
				itemList : changlook,
				success: (res) =>{
					console.log(this.itemList);
					this.yinsi = changlook[res.tapIndex];
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		upload(arr){
			this.imagesList = arr;
		},
		hidePopup(){
			this.popupShow = false;
		}
	},
}
